@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
@import "https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&display=swap";
h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  font-family: Montserrat, sans-serif;
}

html {
  scroll-behavior: smooth;
}

.page {
  box-sizing: border-box;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.page:has(.page__aside:target) {
  overflow: hidden;
}

.page__aside {
  opacity: 0;
  pointer-events: none;
  margin: 0;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__aside:target {
  opacity: 1;
  z-index: 100;
  pointer-events: all;
  transform: translateX(0);
}

.page__header {
  background-color: #fff;
  grid-column: 1 / -1;
}

.page__main {
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-column: 1 / -1;
  display: grid;
}

@media (width >= 320px) {
  .page__main {
    --columns: 2;
    column-gap: 20px;
    padding: 0 20px;
  }
}

@media (width >= 768px) {
  .page__main {
    --columns: 6;
    column-gap: 30px;
    padding: 0 39px;
  }
}

@media (width >= 1280px) {
  .page__main {
    --columns: 12;
    padding: 0 55px;
  }
}

.page__main-title {
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
}

@media (width >= 320px) {
  .page__main-title {
    text-align: center;
  }
}

@media (width >= 768px) {
  .page__main-title {
    text-align: left;
  }
}

.page__main-title--subscription {
  color: #fff;
}

@media (width >= 768px) {
  .page__main-title--subscription {
    text-align: center;
    font-size: 48px;
  }
}

.page__footer {
  grid-column: 1 / -1;
}

.header {
  grid-column: 1 / -1;
  display: flex;
}

@media (width >= 320px) {
  .header {
    flex-direction: column;
    margin: 0 0 70px;
  }
}

@media (width >= 768px) {
  .header {
    flex-direction: row;
    margin: 0 0 88px;
  }
}

@media (width >= 1280px) {
